import React from 'react';
import {useNavigate} from 'react-router-dom';


export default function MenuNavbar() {

    const navigate = useNavigate();

    const displayFeelEnergized = () => {
        navigate('/MenuBoard/FeelEnergized');
    };

    const displayGetFit = () => {
        navigate('/MenuBoard/GetFit');
    };

    const displayManageWeight = () => {
        navigate('/MenuBoard/ManageWeight');
    };

    const displayBeWell = () => {
        navigate('/MenuBoard/BeWell');
    };

    const displayEnjoyTreat = () => {
        navigate('/MenuBoard/EnjoyTreat');
    };

    const displayKidsMenu = () => {
        navigate('/MenuBoard/KidsMenu');
    };


  return (
    <>
        <button onClick={displayFeelEnergized}>Feel Energized</button>
        <button onClick={displayGetFit}>Get Fit</button>
        <button onClick={displayManageWeight}>Manage Weight</button>
        <button onClick={displayBeWell}>Be Well</button>
        <button onClick={displayEnjoyTreat}>Enjoy a Treat</button>
        <button onClick={displayKidsMenu}>Kids Menu</button>
    </>
  )
}
